import { FC, useContext, useEffect, useState } from "react";
import { AppContext } from "../../App";
import { getCandidates } from "../../helpers/api";
import { UserHaveNoProfile } from "../../helpers/errors";
import { IProfile } from "../../interfaces/IProfile";
import Menu from "../Menu/Menu";
import Candidate from "./Candidate";

const Candidates: FC = () => {
  const { setView, personToShowIndex } = useContext(AppContext);

  const [isLoading, setIsLoading] = useState(true);
  const [candidates, setCandidates] = useState<IProfile[]>([]);

  const getCandidatesFromAPI = async () => {
    try {
      const candidates = await getCandidates();

      setCandidates(candidates);
      setIsLoading(false);
    } catch (error) {
      if (error instanceof UserHaveNoProfile) {
        // Check also by res message
        setView("registration");
      }
    }
  };

  useEffect(() => {
    getCandidatesFromAPI();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (isLoading) return null;

  return (
    <>
      {candidates.map((candidate, index) => (
        <Candidate
          profile={candidate}
          // doesn't hide prev cards for showing animation
          style={{
            display: personToShowIndex >= index ? "flex" : "none",
            zIndex: -index,
          }}
          key={index}
        />
      ))}

      <Menu />
    </>
  );
};

export default Candidates;
