import { FC, useState, CSSProperties, ReactNode, useEffect } from "react";
import { CITIES } from "../../constants/cities";
import { TARGETS } from "../../constants/targets";
import { getPhotoURL } from "../../helpers/getPhotoURL";
import { IProfile } from "../../interfaces/IProfile";
import Loading from "../Loading/Loading";
import "./Card.scss";

interface ICardProps {
  profile: IProfile;
  buttons: ReactNode;
  isLiked?: boolean;
  isDisliked?: boolean;
  style?: CSSProperties;
}

const Card: FC<ICardProps> = ({
  profile,
  buttons,
  isLiked = false,
  isDisliked = false,
  style,
}) => {
  const [isLoading, setIsLoading] = useState(true);
  const [photoToShowIndex, setPhotoToShowIndex] = useState(0);

  useEffect(() => {
    profile.photoIds.forEach((photoId) => {
      const img = new Image();
      img.src = getPhotoURL(photoId);
      img.onload = () => {
        setIsLoading(false);
      };
    });
  }, [profile.photoIds]);

  const showNextPhoto = () => {
    setPhotoToShowIndex((prev) =>
      profile.photoIds.length - 1 > prev ? prev + 1 : 0
    );
  };

  const showPrevPhoto = () => {
    setPhotoToShowIndex((prev) =>
      prev > 0 ? prev - 1 : profile.photoIds.length - 1
    );
  };

  if (isLoading) return <Loading />;

  return (
    <>
      <div
        className={`card ${isLiked ? "liked" : ""} ${
          isDisliked ? "disliked" : ""
        }`}
        style={style}
      >
        <div
          className="photo"
          style={{
            backgroundImage: `url(${getPhotoURL(
              profile.photoIds[photoToShowIndex]
            )})`,
          }}
        >
          <div className="photos-counter">
            {profile.photoIds.length > 1 &&
              [...Array(profile.photoIds.length)].map((_, i) => (
                <div
                  className={`slide ${i === photoToShowIndex ? "active" : ""}`}
                  key={i}
                />
              ))}
          </div>
          <div onClick={showPrevPhoto} className="left-side" />
          <div onClick={showNextPhoto} className="right-side" />

          <div className="info">
            <h2>
              {profile.name}, {profile.age}
            </h2>
            <p>
              {CITIES[profile.cityId]},{" "}
              {profile.gender === 1 ? "хлопець" : "дівчина"}
            </p>
            {profile.targetId !== 0 && (
              <p>Ціль: {TARGETS[profile.targetId].toLowerCase()}</p>
            )}
            {profile.description && (
              <p className="description">{profile.description}</p>
            )}
          </div>
        </div>
        {buttons}
      </div>
    </>
  );
};

export default Card;
