import { FC, useContext, useEffect, useRef, useState } from "react";
import { AppContext } from "../../App";
import { getChat } from "../../helpers/api";
import { IChat } from "../../interfaces/IChat";
import "./Chat.scss";

const Chat: FC = () => {
  const { view, setView } = useContext(AppContext);

  const [chat, setChat] = useState<IChat | null>(null);
  const [textareaValue, setTextareaValue] = useState("");
  const [textareaHeight, setTextareaHeight] = useState(36);

  const chatRef = useRef<HTMLDivElement>(null);
  const textareaRef = useRef<HTMLTextAreaElement>(null);

  useEffect(() => {
    setChat(getChat(view.split("chat:")[1]));
  }, [view]);

  useEffect(() => {
    window.scrollTo({
      left: 0,
      top: document.body.scrollHeight,
    });
  }, [chat?.messages]);

  if (!chat) return null;

  return (
    <div ref={chatRef} className="chat">
      <div className="top">
        <span
          onClick={() => setView("chats")}
          className="material-symbols-outlined icon back"
        >
          arrow_back
        </span>
        <div
          onClick={() => {
            setView(`chat-profile:${chat.person.id}`);
          }}
          className="avatar"
          style={{ backgroundImage: `url(${chat.person.avatar})` }}
        />
        <div
          onClick={() => {
            setView(`chat-profile:${chat.person.id}`);
          }}
          className="name"
        >
          {chat.person.name}
        </div>
      </div>

      <div
        className="messages"
        style={{ marginBottom: `calc(54px + ${textareaHeight}px + 15px)` }}
      >
        {chat.messages.map((message, i) => (
          <div
            className="message"
            style={{
              marginLeft: message.isByMe ? "auto" : 0,
            }}
            key={i}
          >
            {message.text.map((text, index) => (
              <div key={index}>
                <span>{text}</span>
                <br />
              </div>
            ))}
          </div>
        ))}
      </div>

      <div className="send">
        <textarea
          onChange={(e) => {
            e.target.style.height = "0";
            e.target.style.height = e.target.scrollHeight + "px";

            setTextareaValue(e.target.value);

            // max-height of the textarea is 300px
            setTextareaHeight(
              e.target.scrollHeight < 300 ? e.target.scrollHeight : 300
            );
          }}
          ref={textareaRef}
          value={textareaValue}
          rows={1}
          placeholder="Повідомлення"
        />
        <span
          onClick={() => {
            textareaRef.current?.focus();
            textareaRef.current!.style.height = "36px";

            setTextareaHeight(36);

            setChat((prev: IChat | null) => {
              if (!prev || !textareaValue) return prev;

              return {
                ...prev,
                messages: [
                  ...prev.messages,
                  { isByMe: true, text: textareaValue.split("\n") },
                ],
              };
            });
            setTextareaValue(""); // redo, possible bug
          }}
          className="material-symbols-outlined icon"
        >
          send
        </span>
        <div className="messages-hidder"></div>
      </div>
    </div>
  );
};

export default Chat;
