import { CSSProperties, FC, useContext, useState } from "react";
import { AppContext } from "../../App";
import { IProfile } from "../../interfaces/IProfile";
import Card from "../Card/Card";

interface ICandidateProps {
  profile: IProfile;
  style?: CSSProperties;
}

const Candidate: FC<ICandidateProps> = ({ profile, style }) => {
  const { setPersonToShowIndex } = useContext(AppContext);

  const [isLiked, setIsLiked] = useState(false);
  const [isDisliked, setIsDisliked] = useState(false);

  const toLike = () => {
    // prevent double clicking
    if (isLiked || isDisliked) return;

    setIsLiked(true);
    setPersonToShowIndex((prev) => prev + 1);
  };

  const toDislike = () => {
    // prevent double clicking
    if (isLiked || isDisliked) return;

    setIsDisliked(true);
    setPersonToShowIndex((prev) => prev + 1);
  };

  return (
    <Card
      profile={profile}
      buttons={
        <div className="buttons">
          <span className="material-symbols-rounded" onClick={toDislike}>
            close
          </span>
          <span
            className="material-symbols-rounded"
            onClick={toLike}
            style={{ color: "#d93025" }}
          >
            favorite
          </span>
        </div>
      }
      isLiked={isLiked}
      isDisliked={isDisliked}
      style={style}
    />
  );
};

export default Candidate;
