import axios, { AxiosError } from "axios";
import { IUserDataWithOptional } from "../interfaces/IUserDataWithOptional";
import { IChat } from "./../interfaces/IChat";
import { IMessagePreview } from "./../interfaces/IMessagePreview";
import { IProfile } from "./../interfaces/IProfile";
import { UserHaveNoProfile } from "./errors";
import { ISignUpInput } from "./interfaces/ISignUpInput";

const BASE_URL =
  "https://35e0gzyyv5.execute-api.eu-central-1.amazonaws.com/prod";

const INIT_DATA_MOCK =
  "query_id=AAELMS8lAAAAAAsxLyUyOPpF&user=%7B%22id%22%3A623849739%2C%22first_name%22%3A%22Bodya%22%2C%22last_name%22%3A%22Dev%22%2C%22username%22%3A%22bodya_dev%22%2C%22language_code%22%3A%22en%22%7D&auth_date=1663282089&hash=ebc32873f81a6b5d7f9c5141bb4aaf70c537b3d56958722837ec8362504ad6fd";
const TEMP_IS_LOCAL = false;

export const getCandidates = async () => {
  try {
    const res = await axios.get(`${BASE_URL}/candidates`, {
      headers: {
        "Init-Data": TEMP_IS_LOCAL
          ? INIT_DATA_MOCK
          : window.Telegram.WebApp.initData,
      },
    });

    return res.data;
  } catch (error) {
    if (error instanceof AxiosError && error.response?.status === 401) {
      throw new UserHaveNoProfile();
    }
  }
};

export const signUp = async (data: ISignUpInput): Promise<boolean> => {
  const res = await axios.post(`${BASE_URL}/sign-up`, data, {
    headers: {
      "Init-Data": TEMP_IS_LOCAL
        ? INIT_DATA_MOCK
        : window.Telegram.WebApp.initData,
    },
  });

  return res.status === 201;
};

export const uploadPhoto = async (base64Photo: string) => {
  const res = await axios.post(
    `${BASE_URL}/photo`,
    { photo: base64Photo },
    {
      headers: {
        "Init-Data": TEMP_IS_LOCAL
          ? INIT_DATA_MOCK
          : window.Telegram.WebApp.initData,
      },
  });

  return res.data; // photoId
};

export const deletePhoto = (photoId: string): void => {
  axios.delete(`${BASE_URL}/photo/${photoId}`, {
    headers: {
      "Init-Data": TEMP_IS_LOCAL
        ? INIT_DATA_MOCK
        : window.Telegram.WebApp.initData,
    },
  });
};

export const getProfile = async (): Promise<IProfile> => {
  const res = await axios.get(`${BASE_URL}/profile`, {
    headers: {
      "Init-Data": TEMP_IS_LOCAL
        ? INIT_DATA_MOCK
        : window.Telegram.WebApp.initData,
    },
  });

  return res.data;
};

export const getUser = async (): Promise<IUserDataWithOptional> => {
  const res = await axios.get(`${BASE_URL}/user`, {
    headers: {
      "Init-Data": TEMP_IS_LOCAL
        ? INIT_DATA_MOCK
        : window.Telegram.WebApp.initData,
    },
  });

  return res.data;
};

export const updateUser = async (
  data: IUserDataWithOptional
): Promise<void> => {
  await axios.put(`${BASE_URL}/user`, data, {
    headers: {
      "Init-Data": TEMP_IS_LOCAL
        ? INIT_DATA_MOCK
        : window.Telegram.WebApp.initData,
    },
  });
};

