import { useCallback, useEffect } from "react";
import { FC, useContext, useState } from "react";
import { AppContext } from "../../App";
import { City } from "../../enum/city";
import { DesiredGender } from "../../enum/desiredGender";
import { Gender } from "../../enum/gender";
import { signUp } from "../../helpers/api";
import { IUserData } from "../../interfaces/IUserData";
import Field from "../Field/Field";
import FieldBirthday from "../Fields/FieldBirthday";
import FieldCity from "../Fields/FieldCity";
import FieldGenders from "../Fields/FieldGenders";
import UploadPhotos from "../UploadPhotos/UploadPhotos";
import "./Registration.scss";

const Registration: FC = () => {
  const { setView } = useContext(AppContext);

  const [step, setStep] = useState(0);
  const [userData, setUserData] = useState<IUserData>({
    photoIds: [],
    name: window.Telegram.WebApp.initDataUnsafe?.user?.first_name || "",
    gender: Gender.NO_SET,
    desiredGender: DesiredGender.NO_SET,
    birthday: "0000-00-00",
    cityId: City.NO_SET,
  });

  const toSignUp = useCallback(async () => {
    const isSuccess = await signUp(userData);

    if (isSuccess) setView("find");
  }, [userData, setView]);

  useEffect(() => {
    if (step >= 3) {
      toSignUp();
    }
  }, [step, toSignUp]);

  return (
    <>
      <div className="registration">
        {step === 0 && (
          <>
            <Field title="Ім'я">
              <input
                type="text"
                value={userData.name}
                onChange={(e) =>
                  setUserData((prev) => ({ ...prev, name: e.target.value }))
                }
                placeholder="Ваше ім'я"
              />
            </Field>
            <FieldGenders userData={userData} setUserData={setUserData} />
          </>
        )}

        {step === 1 && (
          <>
            <FieldBirthday userData={userData} setUserData={setUserData} />
            <FieldCity userData={userData} setUserData={setUserData} />
          </>
        )}

        {step === 2 && (
          <Field title="Фото (мінімум одне)">
            <UploadPhotos />
          </Field>
        )}

        <span
          className="material-symbols-rounded next"
          onClick={() => setStep((prev) => prev + 1)}
        >
          east
        </span>
      </div>
    </>
  );
};

export default Registration;
