import { FC, FocusEventHandler } from "react";
import { TARGETS } from "../../constants/targets";
import { IUserDataWithOptional } from "../../interfaces/IUserDataWithOptional";
import Field from "../Field/Field";

interface IFieldTargetProps {
  userData: IUserDataWithOptional;
  setUserData: Function;
  onBlur?: FocusEventHandler<HTMLSelectElement>;
}

const FieldTarget: FC<IFieldTargetProps> = ({
  userData,
  setUserData,
  onBlur,
}) => {
  return (
    <Field title="Ціль">
      <select
        value={userData.targetId}
        onChange={(e) =>
          setUserData((prev: IUserDataWithOptional) => ({
            ...prev,
            targetId: e.target.value,
          }))
        }
        onBlur={onBlur}
        placeholder="Ціль"
      >
        {TARGETS.map((target, index) => (
          <option value={index} key={`target-${index}`}>
            {target}
          </option>
        ))}
      </select>
    </Field>
  );
};

export default FieldTarget;
