import { FC, useEffect, useRef, useState } from "react";
import { deletePhoto, uploadPhoto } from "../../helpers/api";
import { getPhotoURL } from "../../helpers/getPhotoURL";
import "./UploadPhotos.scss";
import { compressPhotoToFixedSize } from "../../helpers/compressPhotoToFixedSize";
import { getBase64 } from "../../helpers/getBase64";

const MAX_PHOTO_SIZE = 10 * 1024 * 1024; // 10 MB
const MAX_PHOTO_COUNT = 6;

interface IUploadPhotos {
  userPhotoIds?: string[];
}

interface IPhoto {
  photoURL: string;
  photoId?: string;
  tempPhotoId?: number;
}

const UploadPhotos: FC<IUploadPhotos> = ({ userPhotoIds }) => {
  const hiddenFileInput = useRef<HTMLInputElement>(null);
  const [photos, setPhotos] = useState<IPhoto[]>([]);

  useEffect(() => {
    userPhotoIds &&
      setPhotos(
        userPhotoIds.map((photoId) => ({
          photoId,
          photoURL: getPhotoURL(photoId),
        }))
      );
  }, [userPhotoIds]);

  const onAddImage = async (e: React.ChangeEvent<HTMLInputElement>) => {
    if (!e.target.files) return;

    const image = e.target.files[0];

    if (image.size > MAX_PHOTO_SIZE) {
      window.Telegram.WebApp.showAlert("Фото занадто велике (обмеження 10 MB)");
      return;
    }
    console.info("Got photo");

    const photoURL = URL.createObjectURL(image);
    const tempPhotoId = Date.now();
    setPhotos((prev) => [{ photoURL, tempPhotoId }, ...prev]);

    console.info("createdObjectURL");

    const compressedPhoto = await compressPhotoToFixedSize(image);
    console.info("Got compressedPhoto");

    const base64Photo = await getBase64(compressedPhoto);
    console.info("Got base64Photo");

    const photoId = await uploadPhoto(base64Photo);

    setPhotos((prev) =>
      prev.map((photo) => {
        if (photo.tempPhotoId === tempPhotoId) {
          photo.photoId = photoId;
        }

        return photo;
      })
    );
  };

  const removePhoto = (photoId: string) => {
    deletePhoto(photoId);
    setPhotos((prev) => prev.filter((photo) => photo.photoId !== photoId));
  };

  return (
    <div className="upload-photo-container">
      {photos.length < MAX_PHOTO_COUNT && (
        <div
          className="photo-container add-photo"
          onClick={() => hiddenFileInput.current?.click()}
        >
          <span className="material-symbols-outlined add-icon">add</span>
          <input
            type="file"
            hidden
            ref={hiddenFileInput}
            accept="image/jpg, image/png"
            onChange={onAddImage}
          />
        </div>
      )}

      {photos.map((photo) => (
        <div
          style={{ backgroundImage: `url(${photo.photoURL})` }}
          className="photo-container"
          key={photo.photoURL}
        >
          {!photo.photoId && (
            <div className="photo-icon">
              <span className="material-symbols-outlined rotation">
                refresh
              </span>
            </div>
          )}
          {photo.photoId && photos.length > 1 && (
            <div className="photo-icon">
              <span
                className="material-symbols-outlined"
                onClick={() => removePhoto(photo.photoId as string)}
              >
                close
              </span>
            </div>
          )}
        </div>
      ))}
    </div>
  );
};

export default UploadPhotos;
