import { createContext, Dispatch, FC, useState } from "react";
import Menu from "./components/Menu/Menu";
import "./App.scss";
import Edit from "./components/Edit/Edit";
import Messages from "./components/Messages/Messages";
import Registration from "./components/Registration/Registration";
import Chat from "./components/Chat/Chat";
import ProfileFromChat from "./components/Profiles/ProfileFromChat";
import UserProfile from "./components/Profiles/UserProfile";
import Candidates from "./components/Profiles/Candidates";

interface IAppContext {
  view: string;
  setView: Dispatch<React.SetStateAction<string>>;
  personToShowIndex: number;
  setPersonToShowIndex: Dispatch<React.SetStateAction<number>>;
  userProfileUpdater: number;
  setUserProfileUpdater: Dispatch<React.SetStateAction<number>>;
}

export const AppContext = createContext<IAppContext>({
  view: "find",
  setView: () => {},
  personToShowIndex: 0,
  setPersonToShowIndex: () => {},
  userProfileUpdater: 0,
  setUserProfileUpdater: () => {},
});

const App: FC = () => {
  const [view, setView] = useState("find");
  const [personToShowIndex, setPersonToShowIndex] = useState(0);
  const [userProfileUpdater, setUserProfileUpdater] = useState(0);

  return (
    <AppContext.Provider
      value={{
        view,
        setView,
        personToShowIndex,
        setPersonToShowIndex,
        userProfileUpdater,
        setUserProfileUpdater,
      }}
    >
      <div className="app">
        {view === "registration" && <Registration />}
        {view === "find" && <Candidates />}
        {view === "chats" && <Messages />}
        {view.startsWith("chat:") && <Chat />}
        {view.startsWith("chat-profile:") && <ProfileFromChat />}
        {view === "profile" && <UserProfile />}
        {view === "edit" && <Edit />}
        {!["registration", "find"].includes(view) && <Menu />}
      </div>
    </AppContext.Provider>
  );
};

export default App;
