import { FC, useContext, useEffect, useState } from "react";
import { AppContext } from "../../App";
import { getMessages } from "../../helpers/api";
import { IMessagePreview } from "../../interfaces/IMessagePreview";
import "./Messages.scss";

const Messages: FC = () => {
  const { setView } = useContext(AppContext);

  const [messages, setMessages] = useState<IMessagePreview[]>([]);

  useEffect(() => {
    setMessages(getMessages());
  }, []);

  return (
    <div className="chats">
      {messages.map((message, index) => (
        <div onClick={() => setView("chat:2323j2-37sjr-37d")} className="chat-preview" key={`chat-${index}`}>
          <div
            className="avatar"
            style={{ backgroundImage: `url(${message.image})` }}
          />
          <div className="info">
            <div className="name">{message.name}</div>
            <div className="preview-text">
              {message.isFromMe ? "Ви: " : ""}
              {message.previewText}
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default Messages;
