import { FC, useContext, useEffect, useState } from "react";
import { AppContext } from "../../App";
import { getUser, updateUser } from "../../helpers/api";
import { IUserDataWithOptional } from "../../interfaces/IUserDataWithOptional";
import Field from "../Field/Field";
import FieldBirthday from "../Fields/FieldBirthday";
import FieldCity from "../Fields/FieldCity";
import FieldGenders from "../Fields/FieldGenders";
import FieldTarget from "../Fields/FieldTarget";
import UploadPhotos from "../UploadPhotos/UploadPhotos";
import "./Edit.scss";

const Edit: FC = () => {
  const { setUserProfileUpdater } = useContext(AppContext);

  const [user, setUser] = useState<IUserDataWithOptional>();

  const getUserByAPI = async () => {
    setUser(await getUser());
  };

  const updateUserByAPI = async () => {
    if (user) {
      await updateUser(user);
      setUserProfileUpdater((prev) => prev + 1);
    }
  };

  useEffect(() => {
    getUserByAPI();
  }, []);

  if (!user) return null;

  return (
    <div className="edit" style={{ marginBottom: 60 }}>
      <Field title="Фото (мінімум одне)">
        <UploadPhotos userPhotoIds={user.photoIds} />
      </Field>

      <Field title="Ім'я">
        <input
          type="text"
          value={user.name}
          onChange={(e) =>
            setUser((prev) => prev && { ...prev, name: e.target.value })
          }
          onBlur={updateUserByAPI}
          placeholder="Ваше ім'я"
        />
      </Field>

      <FieldGenders
        userData={user}
        setUserData={setUser}
        onBlur={updateUserByAPI}
      />

      <FieldBirthday
        userData={user}
        setUserData={setUser}
        onBlur={updateUserByAPI}
      />

      <FieldCity
        userData={user}
        setUserData={setUser}
        onBlur={updateUserByAPI}
      />

      <FieldTarget
        userData={user}
        setUserData={setUser}
        onBlur={updateUserByAPI}
      />

      <Field title="Опис">
        <textarea
          value={user.description}
          onChange={(e) =>
            setUser((prev) => prev && { ...prev, description: e.target.value })
          }
          onBlur={updateUserByAPI}
          rows={3}
          placeholder="Ваш опис"
        />
      </Field>
    </div>
  );
};

export default Edit;
